<template>
  <label class="vt-checkbox" :class="{ 'is-checked': isChecked }">
    <span class="vt-checkbox_input">
      <span class="vt-checkbox_inner"></span>
      <!-- 单一的复选框，绑定布尔类型值 ：单一使用的时候绑定值为boolean值，用不到value的值，当v-model绑定的值为数组的时候，这个时候会用到:value的值，选中的值会绑定到v-model的数组得值-->
      <!-- 参考网址：https://cn.vuejs.org/guide/essentials/forms.html#text -->
      <input
        type="checkbox"
        class="vt-checkbox_original"
        :name="name"
        v-model="model"
        :value="label"
      />
    </span>
    <span class="vt-checkbox_label">
      <slot></slot>
      <template v-if="!$slots.default">
        {{ label }}
      </template>
    </span>
  </label>
</template>

<script>
export default {
  name: 'vtCheckbox',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    label: {
      type: String,
      default: ''
    },
    name: {
      type: String,
      default: ''
    }
  },
  computed: {
    model: {
      get () {
        // this.CheckboxGroup.value 的值类型是数组 这时绑定的label的值
        return this.isGroup ? this.CheckboxGroup.value : this.value
      },
      set (value) {
        this.isGroup
          ? this.CheckboxGroup.$emit('input', value)
          : this.$emit('input', value)
      }
    },
    isGroup () {
      return !!this.CheckboxGroup
    },
    isChecked () {
      // 如果十group包裹，判断label是否在model中 如果包含的有就返回true
      // 如果没有group包裹,直接使用model
      return this.isGroup ? this.model.includes(this.label) : this.model
    }
  },
  inject: {
    CheckboxGroup: {
      default: ''
    }
  }
}
</script>

<style lang="scss" scoped>
.vt-checkbox {
  color: #606266;
  font-weight: 500;
  font-size: 14px;
  position: relative;
  cursor: pointer;
  display: inline-block;
  white-space: nowrap;
  user-select: none;
  margin-right: 30px;
  .vt-checkbox_input {
    white-space: nowrap;
    cursor: pointer;
    outline: none;
    display: inline-block;
    line-height: 1;
    position: relative;
    vertical-align: middle;
    .vt-checkbox_inner {
      display: inline-block;
      position: relative;
      border: 1px solid #dcdfe6;
      border-radius: 2px;
      box-sizing: border-box;
      width: 14px;
      height: 14px;
      background-color: #fff;
      z-index: 1;
      transition: border-color 0.25s cubic-bezier(0.71, -0.46, 0.29, 1.46),
        background-color 0.25s, cubic-bezier(0.71, -0.46, 0.29, 1.46);
      // 这是一个直角对号，并且沿Y轴顺时针旋转了45度
      &:after {
        box-sizing: content-box;
        content: "";
        border: 1px solid #ffffff;
        border-left: 0;
        border-top: 0;
        // 对号的长的一边
        height: 7px;
        // 短的一边
        left: 4px;
        position: absolute;
        top: 1px;
        //  scaleY(1) 图形沿Y轴进行缩放
        transform: rotate(45deg) scaleY(1);
        width: 3px;
        transition: transform 0.15s ease-in 0.05s;
        transform-origin: center;
      }
    }
    .vt-checkbox_original {
      opacity: 0;
      outline: none;
      position: absolute;
      left: 10px;
      margin: 0;
      width: 0;
      height: 0;
      z-index: -1;
    }
  }
  .vt-checkbox_label {
    display: inline-block;
    padding-left: 10px;
    line-height: 19px;
    font-size: 14px;
  }
}

// 选中的样式
.vt-checkbox.is-checked {
  .vt-checkbox_input {
    .vt-checkbox_inner {
      background-color: #409eff;
      border-color: #409eff;
    }
    &:after {
      transform: rotate(45deg) scaleY(1);
    }
  }
  .vt-checkbox_label {
    color: #409eff;
  }
}
</style>
